import React, {useCallback, useEffect} from 'react';
import {Formik, Form, Field, ErrorMessage, useFormik} from 'formik';
import {Button, Heading, Input} from '@chakra-ui/react'
import {
    FormControl,
    FormLabel,
    FormErrorMessage,
    FormHelperText,
} from '@chakra-ui/react'
import * as yup from "yup";


export const MyForm = () => {
    const formik = useFormik({
        initialValues: {firstName: "", lastName: ""},
        onSubmit: (values) => {
            window.Telegram.WebApp.sendData(JSON.stringify({
                firstName: formik.values.firstName,
                lastName: formik.values.lastName
            }));
            formik.resetForm()
        },
        validationSchema: yup.object(
            {firstName: yup.string().required(), lastName: yup.string().required()
        }),
    })
    const sendDataToTelegram = useCallback(() => {
        formik.handleSubmit()
    }, [formik.values])
    const mainButton = window.Telegram.WebApp.MainButton;
    mainButton.text = "Save Data";
    mainButton.enable();
    mainButton.show();

    useEffect(() => {
        window.Telegram.WebApp.onEvent('mainButtonClicked', sendDataToTelegram)
        return () => {
            window.Telegram.WebApp.offEvent('mainButtonClicked', sendDataToTelegram)
        }
    }, [sendDataToTelegram])


    return(
        <div>
            <Heading textAlign={"center"}>First Form</Heading>
            <form onSubmit={(e) => {
                e.preventDefault();
                formik.handleSubmit(e);
            }}>
                <FormControl isInvalid={formik.errors.firstName && formik.touched.firstName}>
                    <FormLabel>First name</FormLabel>
                    <Input id={"firstName"}
                           name={"firstName"}
                           value={formik.values.firstName}
                           onChange={(e) =>{
                               formik.setFieldValue('firstName', e.target.value)
                           }}
                           onBlur={(e) => {
                               formik.setTouched({...formik.touched, firstName: true})
                           }}
                           placeholder='First name' />
                    <FormErrorMessage>{formik.errors.firstName}</FormErrorMessage>
                </FormControl>
                <FormControl isInvalid={formik.errors.lastName && formik.touched.lastName}>
                    <FormLabel>Last Name</FormLabel>
                    <Input
                        id={"lastName"}
                        name={"lastName"}
                        value={formik.values.lastName}
                        onChange={(e) =>{
                            formik.setFieldValue('lastName', e.target.value)
                        }}
                        onBlur={(e) => {
                            formik.setTouched({...formik.touched, lastName: true})
                        }}

                        placeholder='Last name' />
                    <FormErrorMessage>{formik.errors.lastName}</FormErrorMessage>
                </FormControl>
                {mainButton === undefined ?
                    <Button
                        colorScheme='teal'
                        type='submit'
                        width={"full"}
                    >
                        Submit
                    </Button>: <span></span>
                }
            </form>
        </div>
    )}